.form {
  max-width: 40rem;
  margin: 1.5rem auto;
}

.form label,
.form input,
.form textarea {
  display: block;
  width: 100%;
}

.form input,
.form textarea {
  font: inherit;
  padding: 0.4rem;
  margin-bottom: 0.5rem;
}

.form label {
  color: rgb(194, 194, 194);
}

.actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.actions button {
  font: inherit;
  cursor: pointer;
  padding: 0.5rem 1.5rem;
  border-radius: 4px;
  background-color: var(--color-background);
  color: var(--color-primary);
  border: none;
}

.actions button[type="button"] {
  color: red;
}

.actions button:hover {
  color: var(--color-tertiary);
}
.actions button[type="button"]:hover {
  color: brown;
}
